@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* PORTADA */
.portada-container {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0px 20px;
}

.portada {
  position: relative;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  max-width: 1200px;
  width: 100%;
  grid-gap: 50px;
  gap: 50px;
}

.portada .columna1 {
  position: relative;
  z-index: 600;
  width: 50%;
}
.portada .columna2 {
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
/* *Primera columna */
.container-title {
  position: relative;
}
.portada h1 {
  font-size: clamp(40px, 4vw, 60px);
  font-weight: bolder;
  margin-bottom: clamp(10px, 1.3vw, 20px);
  margin-top: 0;
  line-height: 1.1;
  color: var(--negro);
}

.first-title {
  font-size: clamp(20px, 2.5vw, 40px);
  line-height: 1.1;
  opacity: 1;
  position: relative;
}

.portada .columna1 p {
  line-height: 1.5;
  font-size: clamp(14px, 1.5vw, 17px);
}

.portada .botones {
  margin-top: clamp(20px, 2vw, 30px);
  display: flex;
  grid-gap: 20px;
  gap: 20px;
}
.portada button {
  width: 185px;
  height: clamp(45px, 5.5vw, 65px);
  font-size: clamp(15px, 3vw, 18px);
  font-weight: 500;
  border-radius: 30px;
  border: 2px solid var(--negro);
  background: transparent;
  transition: var(--default-animation);
}
.portada .comencemos {
  background-color: var(--anaranjado);
  color: #fff;
  border: none;
  box-shadow: 4px 2px 10px rgba(0, 0, 0, 0.25);
}
.portada button:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}
.portada .conocenos:hover {
  background-color: rgb(56, 56, 56);
  color: white;
}
/* *Segunda Columna */
.portada img {
  object-fit: cover;
  width: 100%;
  z-index: 600;
}
.imagen-1 {
  position: relative;
  -webkit-animation: desvanecer2 10s infinite;
          animation: desvanecer2 10s infinite;
}
.imagen-2 {
  position: absolute;
  -webkit-animation: desvanecer 10s infinite;
          animation: desvanecer 10s infinite;
}
@-webkit-keyframes desvanecer {
  0% {
    opacity: 0;
  }
  45% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  95% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@keyframes desvanecer {
  0% {
    opacity: 0;
  }
  45% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  95% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}
@-webkit-keyframes desvanecer2 {
  0% {
    opacity: 1;
  }
  45% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  95% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes desvanecer2 {
  0% {
    opacity: 1;
  }
  45% {
    opacity: 1;
  }
  50% {
    opacity: 0;
  }
  95% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
/* *Manchas */
.portada .mancha1,
.portada .mancha2,
.portada .mancha3,
.portada .mancha4 {
  width: 520px;
  height: 370px;
  position: absolute;
  z-index: 500;
}
.portada .mancha1 {
  left: 130px;
  bottom: -285px;
}
.portada .mancha2 {
  left: 310px;
  top: -215px;
}
.portada .mancha3 {
  right: 100px;
  top: 50px;
}
.portada .mancha4 {
  right: -450px;
  bottom: -50px;
}
@media (max-width: 1020px) {
  .portada {
    flex-direction: column;
    max-width: 500px;
    grid-gap: 15px;
    gap: 15px;
  }
  .portada .botones {
    flex-direction: column;
    grid-gap: 15px;
    gap: 15px;
  }
  .botones button {
    width: 100%;
  }
  .portada .columna1,
  .portada .columna2 {
    width: 100%;
    text-align: center;
  }
  .portada p {
    line-height: 1.1;
  }
  .portada img {
    width: 65%;
  }
}

.quejas-container {
  height: 100vh;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0px 20px;
}
.quejas {
  max-width: 1200px;
  width: 100%;
}
/* *Header */
.quejas h2 {
  font-size: clamp(26px, 4vw, 40px);
  font-weight: bolder;
  margin-bottom: 20px;
  text-align: center;
  position: relative;
  z-index: 600;
  color: var(--negro);
}
/* *Content */
.quejas .contenedor {
  display: flex;
  position: relative;
  z-index: 600;
  justify-content: space-evenly;
  height: 400px;
}
.contenedor > * {
  width: 30%;
}
.contenedor .imagen {
  align-self: flex-end;
  position: absolute;
}
.imagen-hombre-en-duda {
  width: 100%;
}
.quejas section {
  display: inline-block;
  padding: clamp(10px, 2vw, 20px) clamp(30px, 2vw, 50px);
  border-radius: 50px 50px 0 50px;
  background-color: #d9d9d9;
  box-shadow: 0px 8px 24px rgba(0, 0, 0, 0.15);
  font-size: clamp(14px, 2vw, 16px);
  line-height: 1.2;
  color: var(--negro);
  z-index: 2;
}
.quejas :nth-child(3) section {
  border-radius: 50px 50px 50px 0;
}
.quejas .preguntas {
  display: flex;
  align-items: center;
  flex-direction: column;
}
.preguntas > * {
  margin-bottom: 30px;
}

@media (max-width: 689px) {
  .quejas .contenedor {
    flex-direction: column;
    align-items: center;
    width: 100%;
    justify-content: start;
  }
  .preguntas {
    width: 100%;
  }
  .preguntas section {
    margin-bottom: 10px;
    width: 70%;
  }
  .contenedor .imagen {
    align-self: center;
    bottom: -30px;
  }
}
@media (max-width: 620px) {
  .contenedor .imagen {
    width: 200px;
  }
}

@media (max-width: 400px) {
  .contenedor .imagen {
    bottom: -50px;
  }
}

.ofrecemos-container {
  height: 120vh;
  overflow: hidden;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0px 20px;
  background-color: #ffbb92;
  z-index: 700;
}
.ofrecemos {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.ofrecemos .fondo {
  position: relative;
  width: 50%;
}
.ofrecemos .circulo {
  border-radius: 50%;
  background-color: #ff815b;
  width: 1000px;
  height: 1000px;
  position: absolute;
  z-index: 100;
  bottom: -400px;
  left: -250px;
}
.ofrecemos img {
  position: relative;
  z-index: 200;
  width: 100%;
}
.ofrecemos .contenido {
  width: 45%;
  z-index: 300;
}
.ofrecemos h2 {
  font-size: clamp(35px, 4vw, 60px);
  color: #fff;
  font-weight: bolder;
  margin-bottom: clamp(5px, 4vw, 15px);
}
.ofrecemos h3 {
  color: #fff;
  font-size: clamp(18px, 2vw, 25px);
  margin-bottom: 5px;
}
.ofrecemos p {
  color: #fff;
  margin-bottom: 10px;
  line-height: 1.3;
  font-size: clamp(12px, 1.5vw, 15px);
  font-weight: 400;
}
.ofrecemos ul {
  margin-left: 70px;
}
.ofrecemos li {
  display: flex;
  align-items: flex-start;
}
.contenido .icons {
  font-size: 25px;
  color: #fff;
  margin-right: 30px;
}
.ofrecemos .botones {
  width: 100%;
  display: flex;
  align-items: center;
  padding-top: 20px;
  grid-gap: 20px;
  gap: 20px;
  margin-left: 70px;
}
.ofrecemos button {
  color: #fff;
  background: transparent;
  border: 3px solid #fff;
  border-radius: 30px;
  font-weight: 600;
  font-size: 18px;
  width: 200px;
  height: 60px;
  transition: var(--default-animation);
}
.ofrecemos button:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
  background-color: white;
  color: var(--negro);
}
.ofrecemos .empecemos {
  color: var(--negro);
  background-color: #fff;
  border: none;
}

@media (max-width: 1510px) {
  .ofrecemos h2 {
    margin-bottom: 30px;
  }
  .ofrecemos .contenido {
    width: 100%;
  }
  .ofrecemos ul,
  .ofrecemos .botones {
    margin-left: 0;
  }
  .ofrecemos .fondo {
    width: 100%;
  }
  .ofrecemos .circulo {
    width: 750px;
    height: 750px;
    bottom: -400px;
    left: -300px;
  }
}
@media screen and (max-width: 767px) {
  .ofrecemos {
    flex-direction: column-reverse;
    max-width: 500px;
  }
  .ofrecemos img {
    width: 60%;
    margin: auto;
  }
  .contenido h2 {
    margin-bottom: 15px;
  }
  .contenido .icons {
    margin-right: 20px;
  }
  .ofrecemos ul,
  .ofrecemos .botones {
    margin-left: 0;
    flex-direction: column;
  }
  .ofrecemos .contenido {
    width: 100%;
  }
  .ofrecemos .fondo {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .ofrecemos .circulo {
    width: 500px;
    height: 500px;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
  }
  .ofrecemos .botones {
    font-size: 18px;
    width: 100%;
    display: none;
  }
  .ofrecemos a {
    width: 100%;
  }
}

.necesitas-container {
