  }
}

@media (max-width: 600px) {
  .partners {
    grid-template-rows: 80px 50px;
  }
}

@media (max-width: 380px) {
  .somos .contenido {
    margin-left: 5px;
    width: 70%;
  }
  .somos .linea {
    align-self: flex-start;
    left: 40px;
    height: 80%;
    margin: 0 -5px;
  }

  .somos .circulo {
    margin: 0 10px;
    min-width: 50px;
    min-height: 50px;
  }
  .somos .icons {
    font-size: 17px;
  }
}

.area-automatizar-container {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0px 20px;
}
.area-automatizar {
  max-width: min(1200px, 100%);
}
.area-automatizar-header h2,
.area-automatizar-header strong {
  font-size: clamp(17px, 4vw, 25px);
  font-weight: 1000;
  line-height: 0.95;
  color: var(--negro);
}
.area-automatizar-header {
  margin-bottom: clamp(8px, 1vw, 10px);
}
.area-automatizar-portada {
  margin-bottom: 30px;
  color: var(--negro);
}
.area-automatizar-portada h2 {
  font-size: clamp(1.8rem, 5vw, 3rem);
  font-weight: bold;
  line-height: 1.1;
  color: var(--negro);
  margin-bottom: clamp(10px, 2vw, 30px);
}
.area-automatizar-portada p {
  font-size: clamp(13px, 2vw, 18px);
  text-align: justify;
  line-height: 1.3;
}

.area-automatizar-opciones .title {
  text-align: center;
  font-weight: 400;
  color: var(--negro);
  font-size: clamp(1.3rem, 4vw, 1.8rem);
  margin-bottom: clamp(20px, 3vw, 30px);
}
.hand-pointer {
  -webkit-animation: cursor 3.5s infinite;
          animation: cursor 3.5s infinite;
}
.hand-pointer-container::before {
  content: "";
  position: absolute;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: rgb(59, 59, 59);
  margin-left: 5px;
  top: -9px;
  -webkit-animation: click 3.5s infinite;
          animation: click 3.5s infinite;
}
@-webkit-keyframes cursor {
  0% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  50% {
    -webkit-transform: translateY(-10px);
